<template>
  <div class="slide-show">
    <div class="slide-img">
      <a :href="slides[nowIndex].href">
        <transition name="slide-trans">
          <img v-if="isShow" :src="slides[nowIndex].url">
        </transition>
        <transition name="slide-trans-old">
          <img v-if="!isShow" :src="slides[nowIndex].url">
        </transition>
      </a>
    </div>
    <h2 class="slide-title">title</h2>
    <ul class="slide-page">
      <li @click='goto(prevIndex)'><</li>
      <li v-for="(item, index) in slides" @click="goto(index)">
        <a href="##" :class="{'on': index === nowIndex}">{{ index + 1 }}</a>
      </li>
      <li @click='goto(nextIndex)'>></li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    slides: {
      type: Array,
      default: []
    },
    inv: {
      type: Number,
      default: 1000
    }
  },
  data () {
    return {
      nowIndex: 0,
      isShow: true
    }
  },
  methods: {
    goto (index) {
      this.isShow = false
      setTimeout(() => {
        this.isShow = true
        this.nowIndex = index
      }, 10)
    }
  },
  computed: {
    prevIndex () {
      if (this.nowIndex === 0) {
        return this.slides.length - 1
      } else {
        return this.nowIndex - 1
      }
    },
    nextIndex () {
      if (this.nowIndex === this.slides.length - 1) {
        return 0
      } else {
        return this.nowIndex + 1
      }
    }
  }
}
</script>
<style>
    .slide-trans-enter-active {
  transition: all 3s;
}
.slide-trans-enter {
  transform: translateX(900px);
}
.slide-trans-old-leave-active {
  transition: all 3s;
  transform: translateX(-900px);
}

  .slide-show{
    width: 900px;
    height: 300px;
    overflow: hidden;
    position: relative;
  }
  .slide-show .slide-img {
    width:100%;
  }
  .slide-show .slide-img img{
    position: absolute;
    top: 0;
    width: 100%;
  }
  .slide-show .slide-title{
    position: absolute;
    height: 30px;
    width: 100%;
    bottom: 0;
    background-color: black;
    opacity: 0.5;
    padding-left: 15px;
  }
  .slide-show .slide-page{
    position: absolute;
    bottom: 10px;
    right: 0;
  }
  .slide-show .slide-page li {
    padding: 0 10px;
    display: inline-block;
    color: white;
    cursor: pointer;
  }
  .slide-show .slide-page li .on {
    color: red;
  }
</style>


